<template>
  <v-skeleton-loader :loading="loading" type="table">
    <v-data-table
      :headers="headers"
      :header-props="{ sortByText: $t('sortBy') }"
      :items="items"
      disable-pagination
      hide-default-footer
    >
      <template v-slot:body="{ items }">
        <tbody>
          <tr v-for="item in items" :key="item.name">
            <td>{{ item.name }}</td>
            <td>
              {{ item.price !== null ? `${item.price} ${currencySymbol}` : `` }}
            </td>
            <td>
              {{
                item.discountThreshold !== null
                  ? `${item.discountThreshold} ${currencySymbol}`
                  : ``
              }}
            </td>
            <td>
              {{
                item.discountedPrice !== null
                  ? `${item.discountedPrice} ${currencySymbol}`
                  : ``
              }}
            </td>
            <td>
              {{
                item.minimalOrderValue !== null
                  ? `${item.minimalOrderValue} ${currencySymbol}`
                  : ``
              }}
            </td>
            <td>
              <v-icon small class="mr-2" @click="editItem(item)"
                >mdi-pencil</v-icon
              >
              <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
            </td>
          </tr>
        </tbody>
      </template>

      <template v-slot:top>
        <v-toolbar flat color="white">
          <div class="flex-grow-1"></div>
          <v-dialog v-model="dialog" max-width="800px" persistent>
            <template v-slot:activator="{ on }">
              <v-btn color="primary" text rounded class="mb-2" v-on="on">{{
                $t("newZone")
              }}</v-btn>
            </template>
            <v-card>
              <v-card-title class="headline">{{ formTitle }}</v-card-title>
              <v-form ref="form" v-model="valid">
                <v-card-text class="pt-0">
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="6" md="8">
                        <v-text-field
                          v-model="editedItem.name"
                          :rules="[rules.required]"
                          :label="`${$t('zoneName')} *`"
                          rounded
                          filled
                          hide-details
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field
                          v-model="editedItem.price"
                          :rules="[rules.required, rules.minimalValue]"
                          type="number"
                          :label="`${$t('deliveryPrice')} *`"
                          rounded
                          filled
                          hide-details
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field
                          v-model="editedItem.discountThreshold"
                          type="number"
                          :label="$t('discountThreshold')"
                          rounded
                          filled
                          hide-details
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field
                          v-model="editedItem.discountedPrice"
                          type="number"
                          :label="$t('discountedPrice')"
                          rounded
                          filled
                          hide-details
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field
                          v-model="editedItem.minimalOrderValue"
                          type="number"
                          :label="$t('minimalOrderValue')"
                          rounded
                          filled
                          hide-details
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <div class="flex-grow-1"></div>
                  <v-btn color="primary" text rounded @click="close">{{
                    $t("close")
                  }}</v-btn>
                  <v-btn
                    color="success"
                    text
                    rounded
                    @click="save"
                    :disabled="!valid"
                    >{{ $t("save") }}</v-btn
                  >
                </v-card-actions>
              </v-form>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:no-data>
        {{ $t("noData") }}
      </template>
    </v-data-table>
  </v-skeleton-loader>
</template>

<script>
import restaurantService from "../services/restaurant.service";

export default {
  data() {
    return {
      loading: true,
      dialog: false,
      valid: true,
      rules: {
        required: (value) =>
          (value !== "" && value !== null) || this.$t("requiredField"),
        minimalValue: (value) =>
          Number(value) >= 0 || this.$t("minimalAmountIs", { amount: 0 }),
      },
      headers: [
        { text: this.$t("name"), value: "name" },
        { text: this.$t("price"), value: "price" },
        { text: this.$t("discountThreshold"), value: "discountThreshold" },
        { text: this.$t("discountedPrice"), value: "discountedPrice" },
        { text: this.$t("minimalOrderValue"), value: "minimalOrderValue" },
        { text: this.$t("actions"), value: "action", sortable: false },
      ],
      items: [],
      editedIndex: -1,
      editedItem: {
        name: "",
        price: 0,
        discountedPrice: null,
        discountThreshold: null,
        minimalOrderValue: null,
      },
      defaultItem: {
        name: "",
        price: 0,
        discountedPrice: null,
        discountThreshold: null,
        minimalOrderValue: null,
      },
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("addNewZone")
        : this.$t("editZone");
    },
    currencySymbol() {
      return localStorage.currencySymbol;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    async initialize() {
      const resp = await restaurantService.getRestaurant(
        localStorage.getItem("restaurantId"),
        "deliveryOptions"
      );
      this.items = resp.delivery.zones;
      this.loading = false;
    },
    editItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async deleteItem(item) {
      if (window.confirm(this.$t("areYouSureYouWantToDeleteThisZone"))) {
        const resp = await restaurantService.removeZone(
          localStorage.getItem("restaurantId"),
          item._id
        );
        this.items = resp;
      }
    },
    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    async save() {
      if (this.editedIndex > -1) {
        const resp = await restaurantService.editZone(
          localStorage.getItem("restaurantId"),
          this.editedItem
        );
        this.items = resp;
      } else {
        const resp = await restaurantService.addZone(
          localStorage.getItem("restaurantId"),
          this.editedItem
        );
        this.items = resp;
      }
      this.close();
    },
  },
};
</script>

<style scoped>
.active-category-class {
  font-weight: bold;
}

.v-data-table .v-toolbar__content {
  float: left;
  padding: 0;
}

.v-btn--round .v-btn__content .v-icon {
  color: unset !important;
}
</style>